import { useCallback } from "react"
import { useNavigate } from "react-router-dom"

export const useBackOrNavigate = () => {
  const navigate = useNavigate()
  return useCallback(
    (route: string) => {
      if (window.history.state?.idx > 0) {
        navigate(-1)
      } else {
        navigate(route)
      }
    },
    [navigate],
  )
}
